* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: #FFE451;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100px;
  margin: 30px 0;
}

@media all and (min-width: 768px) {
  .form {
    margin: 50px 0;
  }
}

.form__title {
  font-size: 22px;
  text-align: center;
}

@media all and (min-width: 768px) {
  .form__title {
    font-size: 36px;
  }
}

.form__input {
  height: 30px;
  width: 65%;
  max-width: 420px;
  text-align: center;
  border: none;
  box-shadow: 3px 4px 25px 0px #6d6868;
}
.pokemon__card {
  width: 200px;
  height: 200px;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 15px;
  box-shadow: 3px 6px 15px 0px #6D6868;
}

.image__container {
  background-color: #DEDEDE;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.pokemon__card-image {
  align-self: center;
}

.pokemon__card-number {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 20px;
  font-size: 12px;
  background-color: #BBBAB8;
  color: #82664B;
}

.pokemon__card-data {
  background-color: #FFF;
  height: 80px;
  padding-left: 10px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}

.data__name {
  margin-top: 5px;
  text-transform: capitalize;
}

.data__evolve {
  font-size: 12px;
}

.data__types {
  list-style: none;
  display: flex;
  margin-top: 5px;
}

.data__types-type {
  border: 1px solid #82664B;
  border-radius: 6px;
  display: block;
  margin-right: 10px;
  padding: 5px;
  font-size: 12px;
  text-transform: uppercase;
  color: #82664B;
}


.pokemon__list {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  list-style: none;
}
.pokemon__detail-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pokemon__detail-main {
  height: 80%;
  display: flex;
  flex-direction: column;
}

.pokemon__detail-image {
  width: 70%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 0 auto;
}

.pokemon__detail-data {
  margin-left: 20px;
}

.pokemon__detail-name {
  text-align: center;
  margin-bottom: 20px;
  text-transform: capitalize;
}

.description-text {
  padding-bottom: 5px;
}

.description-tittle {
  font-size: 16px;
  padding: 10px 0;
}

.description-list-abilities {
  list-style: none;
  display: flex;  
}

.description-item-abilities {
  padding: 0 20px 0 0;
}

.description-list-evolution {
  list-style: none;
}

.description-item-evolution {

}

.pokemon__detail-link {
  justify-self: flex-end;
  align-self: center;
}

